html{
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: #43c6ac; /* fallback for old browsers */
  background-image: -webkit-linear-gradient(to top, #43c6ac, #191654); /* Chrome 10-25, Safari 5.1-6 */
  background-image: -webkit-linear-gradient(bottom, #43c6ac, #191654);
  background-image: linear-gradient(to top, #43c6ac, #191654);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  
}

body {
  background: red;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  margin-top: 20%;
}


.App {
  text-align: center;
  height: 100%;
  margin: 0;
  background: transparent;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.load-container {
  width: 100%;
  height: 100%;
}

.load-wrapper {
  width: 60px;
  height: 60px;
  margin: 0 auto;
  margin-top: 50vh;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.container-fluid {
  max-width: 100%;
  height: 100vh;
  background-color: transparent;
  z-index: 1;
}
.bg {
    position: fixed;
    left: 0;
    right: 0;
    z-index: -1;
    display: block;
    background-image: url('https://media2.fdncms.com/chicago/imager/u/original/27089989/best_fancy_restaurant-girl_and_the_goat.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    -webkit-filter: brightness(.5);
    -moz-filter: brightness(.5);
    -o-filter: brightness(.5);
    -ms-filter: brightness(.5);
    filter: brightness(.5);
    width: 100%;
    height: 100vh;
  
}

.restaurant-card {
  max-width: 75%;
  margin: 0 auto;
  margin-top: 30vh;
  box-shadow: 5px 5px 5px black;
}

.btn {
  box-shadow: 3px 3px 5px #2f3d41;
  padding-bottom: 70px;
}

/*appear - on page load */
.fade-appear {
  opacity: 0;
  z-index: 1;
}

.fade-appear.fade-appear-active {
  opacity: 1;
  -webkit-transition: opacity 900ms linear;
  transition: opacity 900ms linear;
}

/* enter */
.fade-enter {
  opacity: 0;
  z-index: 1;
}
.fade-enter.fade-enter-active {
    opacity: 1;
    -webkit-transition: opacity 50900ms linear 50000ms;
    transition: opacity 50900ms linear 50000ms;
}

/* exit */
.fade-exit {
    opacity: 1;
}
.fade-exit.fade-exit-active {
    opacity: 0;
    -webkit-transition: opacity 50000ms linear;
    transition: opacity 50000ms linear;
}
.fade-exit-done {
  opacity: 0;
}

